*/
<template>
  <div>
    <van-pull-refresh v-model="isRefreshing" @refresh="onRefresh">
      <van-list @load="onListLoadMore" class="overflow-auto" v-model="isLoading" :style="listHeight"
                ref="list"
                :offset="200"
                :finished="isFinished"
                finished-text="没有更多了"
                error-text="请求失败，点击重新加载"
                :error.sync="isError">
        <van-cell v-for="(item,index) in tableData" :key="index" @click="onListItemClick(item)">
          <div class="cell">
            <div>
              <div class="top-line">
                <span class="title">{{getMissionTypeName(item)}}</span>
                <span
                  class="description">{{item.title}}</span>
              </div>
              <div class="bottom-line">
                <span>任务编号：{{item.missionId}}</span>
                <span class="price">单价：{{item.price / 100}}元</span>
              </div>
              <div class="bottom-line">
                <span>剩余数量：{{item.count}}</span>
              </div>
              <div class="bottom-line">
                <span>置顶结束时间：{{item.topEndTime}}</span>
              </div>
              <div v-show="item.statusPropertyId > 28">
                <div class="bottom-line">
                  <span>待审核数量：{{item.waitingReviewCount}}</span>
                </div>
                <div class="bottom-line">
                  <span>已完成数量：{{item.finishCount}}</span>
                </div>
                <div class="bottom-line">
                  <span>不合格数量：{{item.rejectedCount}}</span>
                </div>
              </div>
              <div class="bottom-line">
                <span>任务状态：{{getMissionStatusName(item)}}</span>
              </div>
            </div>
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import { MY_PUBLISH_MISSION_LIST_URL } from "@/config/host"
  import { getPropertyById } from "@/service/property"

  export default {
    name: "MyMissionPublish",
    data () {
      return {
        tableData: [],
        isRefreshing: false,
        isLoading: false,
        isError: false,
        isFinished: false,
        pageNo: 0,
        pageSize: 10,
        listHeight: {
          height: "100%"
        }
      }
    },
    props: {
      willShow: {
        type: Boolean
      }
    },
    mounted () {
      this.listHeight.height = this.$el.parentNode.offsetHeight + "px"
    },
    watch: {
      willShow: function (newData, oldData) {
        if (newData) {
          if (this.tableData.length === 0) {
            this.loadData()
          }
        }
      }
    },
    methods: {
      async loadData () {
        const result = await this.httpPost(MY_PUBLISH_MISSION_LIST_URL, {
          pageNo: this.pageNo,
          pageSize: this.pageSize
        })
        if (result.success) {
          if (result.data.current === 1) {
            this.tableData = result.data.records
          } else {
            this.tableData = this.tableData.concat(result.data.records)
          }
          if (result.data.current === result.data.pages || result.data.records.length === 0) {
            this.isFinished = true
          }
        } else {
          this.isError = true
        }
        this.isRefreshing = false
        this.isLoading = false
      },
      async onTopBarItemClick (item, index) {
        if (this.topBarActiveIndex !== index) {
          this.topBarActiveIndex = index
          this.pageNo = 0
          this.isFinished = false
        }
      },
      getMissionTypeName (item) {
        const p = getPropertyById(item.typePropertyId)
        return p.name
      },
      onListLoadMore () {
        this.pageNo++
        this.loadData()
      },
      onListItemClick (item) {
        this.$router.push({ name: "MyNeedReview", query: { id: item.missionId, title: item.title } })
      },
      onRefresh () {
        this.pageNo = 1
        this.loadData()
      },
      getMissionStatusName (item) {
        const property = getPropertyById(item.statusPropertyId)
        return property.name
      }
    }
  }
</script>

<style scoped lang="less">

  .cell {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      font-weight: bold;
      font-size: 16px;
    }

    .description {
      margin-left: 20px;
      color: #676767;
      width: 200px;
      height: 30px;
      display: block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }

    .top-line {
      display: flex;
    }

    .bottom-line {
      color: #a8a8a8;

      .price {
        margin-left: 20px;
      }
    }

  }
</style>
