* @Description:
*/
<template>
  <div>
    <div ref="menuBar">
      <div class="orderBar">
        <!--上边的排序筛选-->
        <div v-for="(item,index) in orderType" :key="index"
             @click="onTopBarItemClick(item,index)">
          <span :class="{'order-selected':topBarActiveIndex === index}">{{item}}</span>
        </div>
      </div>
      <div class="topBarBottomLine"></div>
    </div>
    <van-pull-refresh v-model="isRefreshing" @refresh="onRefresh">
      <van-list @load="onListLoadMore" class=" overflow-auto" :style="listHeight" v-model="isLoading"
                ref="list"
                :offset="200"
                :finished="isFinished"
                finished-text="没有更多了"
                error-text="请求失败，点击重新加载"
                :error.sync="isError">
        <van-cell v-for="(item,index) in tableData" :key="index" @click="onItemPressed(item)">
          <div class="cell">
            <div>
              <div class="top-line">
                <span class="title">{{getMissionTypeName(item)}}</span>
                <span
                  class="description">{{item.title}}</span>
              </div>
              <div class="bottom-line">
                <span>任务编号：{{item.missionId}}</span>
                <span class="price">单价：{{item.price / 100}}元</span>
              </div>
            </div>
            <van-button plain type="danger" @click.stop="onUploadImgPressed(item)"
                        v-show="item.proceedPropertyId === 21">
              上传验证
            </van-button>
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import { MY_ACCEPT_MISSION_LIST_URL } from "@/config/host"
  import { getPropertyById } from "@/service/property"

  export default {
    name: "MyMissionAccept",
    data () {
      return {
        orderType: ["全部", "待提交", "待审核", "不合格", "已完成"],
        topBarActiveIndex: 0,
        tableData: [],
        isRefreshing: false,
        isLoading: false,
        isError: false,
        isFinished: false,
        pageNo: 0,
        pageSize: 10,
        listHeight: {
          height: "100%"
        },
        isGotoFinishPage: false
      }
    },
    activated () {
      if (this.isGotoFinishPage) {
        this.loadData()
        this.isGotoFinishPage = false
      }
    },
    mounted () {
      this.listHeight.height = this.$el.parentNode.offsetHeight - this.$refs.menuBar.offsetHeight + "px"
    },
    methods: {
      async loadData () {
        const result = await this.httpPost(MY_ACCEPT_MISSION_LIST_URL, {
          pageNo: this.pageNo,
          pageSize: this.pageSize,
          orderType: this.topBarActiveIndex
        })
        if (result.success) {
          if (result.data.current === 1) {
            this.tableData = result.data.records
          } else {
            this.tableData = this.tableData.concat(result.data.records)
          }
          if (result.data.current === result.data.pages || result.data.records.length === 0) {
            this.isFinished = true
          }
        } else {
          this.isError = true
        }
        this.isRefreshing = false
        this.isLoading = false
      },
      async onTopBarItemClick (item, index) {
        if (this.topBarActiveIndex !== index) {
          this.topBarActiveIndex = index
          this.pageNo = 0
          this.isFinished = false
        }
      },
      getMissionTypeName (item) {
        const p = getPropertyById(item.typePropertyId)
        return p.name
      },
      onListLoadMore () {
        this.pageNo++
        this.loadData()
      },
      onUploadImgPressed (item) {
        console.log("111")
        this.isGotoFinishPage = true
        this.$router.push({ name: "FinishMission", query: { id: item.missionId } })
      },
      onItemPressed (item) {
        if (item.proceedPropertyId > 21) {
          this.$router.push({ name: "MissionReview", query: { id: item.acceptId } })
        }
      },
      onRefresh () {
        this.pageNo = 1
        this.loadData()
      }
    }
  }
</script>

<style scoped lang="less">

  .cell {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      font-weight: bold;
      font-size: 16px;
    }

    .description {
      margin-left: 20px;
      color: #676767;
      width: 200px;
      height: 30px;
      display: block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }

    .top-line {
      display: flex;
    }

    .bottom-line {
      color: #a8a8a8;

      .price {
        margin-left: 20px;
      }
    }

  }
</style>
